@font-face {
    font-family: 'RedDeadCyrillic2';
    src: url('fonts/RedDead/RedDeadCyrillic2.eot');
    src: url('fonts/RedDead/RedDeadCyrillic2.eot?#iefix') format('embedded-opentype'),
        url('fonts/RedDead/RedDeadCyrillic2.woff2') format('woff2'),
        url('fonts/RedDead/RedDeadCyrillic2.woff') format('woff'),
        url('fonts/RedDead/RedDeadCyrillic2.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Dobrozrachniy';
    src: url('fonts/dobro/Dobrozrachniy-Regular.eot');
    src: url('fonts/dobro/Dobrozrachniy-Regular.eot?#iefix') format('embedded-opentype'),
        url('fonts/dobro/Dobrozrachniy-Regular.woff2') format('woff2'),
        url('fonts/dobro/Dobrozrachniy-Regular.woff') format('woff'),
        url('fonts/dobro/Dobrozrachniy-Regular.ttf') format('truetype'),
        url('fonts/dobro/Dobrozrachniy-Regular.svg#Dobrozrachniy-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Facon';
    src: url('fonts/facon/Facon.eot');
    src: url('fonts/facon/Facon.eot?#iefix') format('embedded-opentype'),
        url('fonts/facon/Facon.woff2') format('woff2'),
        url('fonts/facon/Facon.woff') format('woff'),
        url('fonts/facon/Facon.ttf') format('truetype'),
        url('fonts/facon/Facon.svg#Facon') format('svg');
    font-weight: bold;
    font-style: italic;
    font-display: swap;
}
@font-face {
    font-family: 'bad_signal_400webfont';
    src: url('fonts/bad/bad_signal_400webfont.eot');
    src: url('fonts/bad/bad_signal_400webfont.eot') format('embedded-opentype'),
         url('fonts/bad/bad_signal_400webfont.woff') format('woff'),
         url('fonts/bad/bad_signal_400webfont.ttf') format('truetype'),
         url('fonts/bad/bad_signal_400webfont.svg') format('svg');
         
}



