@import "variables.scss";
@import "fonts.scss";
//width of content area
@mixin content-width{
    margin: 0 auto;
    max-width: 1200px;
}
//hover animation settings
@mixin animation-settings {
    transition: .3s;
    /* transition-delay: .1s; */
}
//font style
@mixin header-font {
    font-family: 'Russo One', sans-serif;
}
@mixin font-phone {
    font-family: 'RedDeadCyrillic2';
}
// font anouncment block
//  location font
@mixin font-location {
    font-family: 'Dobrozrachniy';
}
@mixin font-date {
    font-family: 'Facon';
}
@mixin font-city {
    font-family: 'Russo One', sans-serif;
}
@mixin font-time {
    font-family: 'Sedgwick Ave Display', cursive;
}