@import "mixins.scss";
@import "variables.scss";

///////////////////////////////////////
    // media style 
@media only screen and (max-width: 1024px){
    body{
        header{
            .header_content_wrap{
                display: flex;
                flex-direction: column;
                justify-content: space-between;
        
                padding-left: 10px;
                padding-right: 10px;
                @include content-width;
                .logo_wrap{
                    a{
                        img{
                            width: 650px;
                        }
                    }
                    margin-bottom: 25px;
                }
                .navigation_wrap{
                    /* display: none; */
                    .nav_list{
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        .nav_item{
                            margin-right: 45px;
                            padding: 7px 20px;
                            border-radius: 10px;
                            background-color: #fff;
                            text-align: center;
                            a{
                                color: #000051;
                                
                            }
                        }
                        .nav_item:hover{
                            background: none;
                            border-bottom: 3px solid transparent;
                        }
                        .nav_item:hover a{
                            color: #fff;
                        }
                    }
                }
            }
        }
        .cover_wrap{
            .phone_wrap{
               margin-top: 100px;
               margin-bottom: 50px;
      
               display: flex;
               justify-content: space-between;
               flex-wrap: no-wrap;
               .phone_text{
                   font-size: 40px;
               }
            }
        }
    }
}
@media only screen and (max-width: 900px) {
    body{
        header{
            .header_content_wrap{
                .logo_wrap{
                    a{
                        img{
                            width: 750px;
                        }
                    }
                    margin-bottom: 25px;
                }
                .navigation_wrap{
                    .nav_list{
                        .nav_item{
                            margin-right: 45px;
                            padding: 7px 20px;
                            border-radius: 10px;
                            background-color: #fff;
                            a{
                                color: #000051;
                            }
                        }
                    }
                }
            }
        }
        .cover_wrap{
            .phone_wrap{
               .phone_text{
                   font-size: 35px;
               }
            }
        }
    }
}
@media only screen and (max-width: 800px) {
    body{
        header{
            .header_content_wrap{
                .logo_wrap{
                    a{
                        img{
                            width: 650px;
                        }
                    }
                    margin-bottom: 15px;
                }
                .arrow-wrap{
                    margin: 5px;
                    display: block;
                    .arrow{
                        width: 25px;
                    }
                }
                .navigation_wrap{
                    display: none;
                    margin-top: 25px;
                    .nav_list{
                        display: flex;
                        flex-direction: column;
                        .nav_item{
                            margin-right: 0;
                            padding-top: 7px;
                            padding-bottom: 7px;
                            background: none;
                            border-radius: 0;
                            
                            a{
                                color: #fff;
                            }
                        }
                    }
                }
            }
        }
        .cover_wrap{
            .phone_wrap{
               .phone_text{
                   font-size: 30px;
               }
            }
        }
    }
}
