body{
   @import 'header.scss';
   .cover_wrap{
      width: 100%;
      height: auto;
      background-image: url(../img/background/cover_bg.png);
      background-size: cover;
      background-position: center;

      display: flex;
      flex-direction: column;
      align-items: center;
      .logo_wrap{
         margin: 10px;
         .logo{
            width: 700px;
         }
      }
      .phone_wrap{
         margin-top: 100px;
         margin-bottom: 50px;

         display: flex;
         justify-content: space-between;
         .phone_text{
            text-shadow: 1px 1px 20px #ff026b;
            
            @include font-phone;

            font-size: 65px;
            color: #ffffff;

            margin: 5px 50px;
         }
      }
   }
   .announcement_wrap{
      width: 100%;
      height: auto;
      background-image: url(../img/background/anouncment_bg.png);
      background-size: cover;
      background-position: center;

      z-index: 2;

      box-shadow: 0 0 20px #000000;
      .content_wrap{
         margin: 0 auto;
         padding-top: 35px;
         padding-bottom: 35px;

         display: flex;
         justify-content: center;
         .image_wrap{
            position: relative;
            .img_lord{
               width: 420px;
               height: 360px;
               position: absolute;
               left: -375px;
               @include animation-settings;
            }
            .img_lord:hover{
               transform-origin: center center;
               transform: scale(1.1);
               left: -400px;
            }
            .img_spide{
               width: 384px;
               height: 304px;

               position: absolute;

               @include animation-settings;

               animation-name: spide-position;
            }
            .img_spide:hover{
               transform-origin: center center;
               transform: scale(1.1);
            }
         }
         .text-block-wrap{
            display: flex;
            justify-content: center;
            margin-left: 25px;
            margin-right: 25px;
            .elements-wrap{
               .items_elements{
                  position: relative;
                  z-index: 10;
                  .arrow_3{
                     position: absolute;

                     top: -30px;
                     left: -25px;

                     animation: arrow3 2s ease-in-out infinite ;
                     /* animation-delay: 1s; */
                  }
                  @keyframes arrow3 {
                     0%{
                        left: -30px;
                        top: -25px;
                     }
                     50% {
                        left: -40px;
                        top: -35px;
                     }
                     100% {
                        left: -30px;
                        top: -25px;
                     }
                  }

                  .arrow_2{
                     position: absolute;

                     top: 50px;
                     left: -35px;

                     animation: arrow2 3s ease-in-out infinite ;
                     /* animation-delay: 1s; */
                  }
                  @keyframes arrow2 {
                     0%{
                        top: 50px;
                        left: -35px;
                     }
                     50% {
                        top: 40px;
                        left: -55px;
                     }
                     100% {
                        top: 50px;
                        left: -35px;
                     }
                  }
                  .arrow_1{
                     position: absolute;

                     top: 200px;
                     left: -65px;

                     animation: arrow1 2.5s ease-in-out infinite ;
                     /* animation-delay: 1s; */
                  }
                  @keyframes arrow1 {
                     0%{
                        top: 200px;
                        left: -65px;
                     }
                     50% {
                        top: 180px;
                        left: -30px;
                     }
                     100% {
                        top: 200px;
                        left: -65px;
                     }
                  }
                  .arrow_4{
                     position: absolute;

                     top: 140px;
                     left: -5px;

                     animation: arrow4 2.5s ease-in-out infinite ;
                     /* animation-delay: 1s; */
                  }
                  @keyframes arrow4 {
                     0%{
                        top: 140px;
                        left: -5px;
                     }
                     50% {
                        top: 140px;
                        left: -30px;
                     }
                     100% {
                        top: 140px;
                        left: -5px;
                     }
                  }
                  .arrow_5{
                     position: absolute;

                     top: 210px;
                     left: -25px;

                     animation: arrow5 2.5s ease-in-out infinite ;
                     /* animation-delay: 1s; */
                  }
                  @keyframes arrow5 {
                     0%{
                        top: 210px;
                        left: -25px;
                     }
                     50% {
                        top: 230px;
                        left: 0;
                     }
                     100% {
                        top: 210px;
                        left: -25px;
                     }
                  }
                  .arrow_6{
                     position: absolute;

                     top: 230px;
                     left: -90px;

                     animation: arrow6 2.5s ease-in-out infinite ;
                     /* animation-delay: 1s; */
                  }
                  @keyframes arrow6 {
                     0%{
                        top: 230px;
                        left: -90px;
                     }
                     50% {
                        top: 200px;
                        left: -100;
                     }
                     100% {
                        top: 230px;
                        left: -90px;
                     }
                  }
               }
            }
            .text-block{
               width: 588px;
               height: 355px;
               background-image: url(../img/img-png/geometry-01.png);
               background-repeat: no-repeat;
               background-position: center;

               position: relative;
               .date{
                  @include font-date;
                  font-size: 48px;
                  color: #4C089A;
                  letter-spacing: 100;

                  position: absolute;

                  top: 30px;
                  left: 140px;

                  transform: rotate(-4deg)
               }
               .city{
                  @include font-city;
                  font-size: 87px;
                  font-style: italic;
                  letter-spacing: 3px;
                  text-transform: uppercase;
                  color: #fff;
                  letter-spacing: 50;

                  position: absolute;

                  top: 85px;
                  left: 60px;

                  transform: rotate(-3deg)
               }
               .location{
                  @include font-location;
                  font-size: 63px;
                  font-style: italic;
                  color: #fff;

                  position: absolute;
                  letter-spacing: 50;

                  position: absolute;

                  top: 155px;
                  left: 90px;

                  transform: rotate(-3deg)
               }
               .time{
                  @include font-time;
                  font-size: 86px;
                  font-style: italic;
                  color: #F9FBFC;
                  letter-spacing: 50;

                  position: absolute;

                  position: absolute;

                  top: 245px;
                  left: 110px;

                  transform: rotate(-3deg);
                  sup{
                     font-size: 40px;
                     vertical-align: super;
                  }
               }
            }
            /* .text-block:hover{
               transform-origin: center center;
	            transform: scale(1.05);
            } */
         }
      }
   }
}
@import "media.scss";
